/* ------------------------------------------------ */
// FEATURES SECTION
/* ------------------------------------------------ */
.section-meals {
  padding: 0;

  .meals-showcase {
    list-style: none;
    width: 100%;

    li {
      display: block;
      width: 25%;
      float: left;

      .meal-photo {
        width: 100%;
        margin: 0;
        overflow: hidden;
        background-color: #000;

        a {
          text-decoration: none;

          img {
            opacity: 0.7;
            width: 100%;
            height: auto;
            transform: scale(1.15);
            transition: transform 0.5s ease-in, opacity 0.5s ease-in;
          }

          img:hover {
            transform: scale(1.04);
            opacity: 1;
          }
        }
      }
    }
  }
}
